<template>
  <div v-if="index === 0" id="ball"></div>
  <div v-if="index === 0" class="container">
    <div id="main">
      <div id="title">WordIn</div>
      <div class="colbox">
        <div @click="change(1)" class="button">
          背诵
        </div>
        <div @click="change(2)" class="button">
          编辑单词本
        </div>
      </div>
    </div>
  </div>
  <div v-if="index === 1" class="container">
    <el-page-header @back="change(0)">
      <template #content>
        <span class="text-large font-600 mr-3"> 背诵 </span>
      </template>
    </el-page-header>
    <recite></recite>
  </div>
  <div v-if="index === 2" class="container">
    <el-page-header @back="change(0)">
      <template #content>
        <span class="text-large font-600 mr-3"> 编辑单词本 </span>
      </template>
    </el-page-header>
    <editor></editor>
  </div>
</template>

<script>
import editor from './components/Editor.vue'
import recite from './components/Recite.vue'

export default {
  name: 'App',
  components: {
    editor, recite,
},
  data() {
    return {
      index: 0,
    }
  },
  methods: {
    change(index) {
      this.index = index;
    }
  },
  created() {
    let wordsets = localStorage.getItem("wordsets");
    if (wordsets) {
      window.wordsets = JSON.parse(wordsets);
    } else window.wordsets = [];
  }
}
</script>

