<template>
    <div v-if="mode === 0">
        <div class="card" style="margin-top: 20px;background-color: white;width: 50%;">
            <div class="title">选择测验范围：</div>
            <el-checkbox v-model="checkAll" :indeterminate="isIndeterminate" @change="handleCheckAllChange"
                size="large">全选</el-checkbox>
            <el-checkbox-group v-model="checkedSets" @change="handleChange">
                <el-checkbox class="checkbox" v-for="wordset in wordsets" :key="wordset" :label="wordset" size="large"
                    border>
                    <div style="font-size: 25px;font-weight: 500;">
                        {{ wordset }}
                    </div>
                </el-checkbox>
            </el-checkbox-group>
        </div>
        <el-button style="margin: 20px;width: 50%;" type="primary" @click="init()">开始背诵</el-button>
    </div>
    <div id="test" v-if="mode === 1">
        <div id="status">背诵进度: {{ current + 1 }}/{{ total }}</div>
        <div>
            <textarea autofocus id="input" v-if="current === answered" @input="change" class="answer" spellcheck="false">
            </textarea>
            <div v-if="current < answered" class="answer">{{ word.word }}</div>
        </div>
        <p id="explain">
            <el-button v-if="current > 0" @click="prev">上一个</el-button>
            <el-button v-if="current < answered" @click="next">下一个</el-button>
            <el-button v-if="current === answered" @click="showAnswer">显示答案</el-button>
            <el-button @click="terminate" type="danger">停止背诵</el-button>
            <box-icon class="btn" style="margin-left: 10px;translate: 0 4px;" @click="audio_play"
                name='volume-full'></box-icon>
        </p>
        <div id="trans">{{ word.type }} {{ word.trans }}</div>
    </div>
</template>

<script>
import { ElMessage, ElNotification, ElMessageBox } from 'element-plus';
import { nextTick } from 'vue';
function randint(a, b) {
    return parseInt(Math.random() * (b - a) + a);
}

export default {
    name: "WordRecite",
    data() {
        return {
            mode: 0,
            wordsets: [],
            checkedSets: [],
            isIndeterminate: false,
            checkAll: false,
            testWords: [],
            seq: [],
            total: 0,
            current: 0,
            answered: 0,
            word: {},
        };
    },
    methods: {
        handleCheckAllChange(val) {
            this.checkedSets = val ? this.wordsets : [];
            this.isIndeterminate = false;
        },
        handleChange(value) {
            this.checkAll = value.length === this.wordsets.length;
            this.isIndeterminate = value.length > 0 && value.length < this.wordsets.length;
        },
        init() {
            if (this.checkedSets.length <= 0) {
                ElMessage({
                    message: "请至少选择一个单词本",
                    type: "error"
                })
                return;
            }
            for (let i of this.checkedSets) {
                let words = JSON.parse(localStorage.getItem(i));
                this.testWords = this.testWords.concat(...words);
            }
            this.total = this.testWords.length;
            let a = Array.from(new Array(this.total).keys());
            for (let i = 0; i < this.total; i++) {
                let rand = randint(0, this.total);
                [a[i], a[rand]] = [a[rand], a[i]];
            }
            this.seq = a;
            this.current = 0;
            this.answered = 0;
            this.mode = 1;
            nextTick(() => {
                this.show();
            });
        },
        next() {
            this.current++;
            if (this.current === this.total) {
                this.mode = 0;
                ElNotification({
                    message: "您已完成所有单词的背诵",
                    title: "Congratulations!",
                    type: "success"
                })
                return;
            }
            nextTick(() => {
                this.show();
            });
        },
        prev() {
            this.current--;
            this.show();
        },
        showAnswer() {
            ElMessage(`答案：${this.word.word}`);
        },
        show() {
            this.word = this.testWords[this.seq[this.current]];
            console.log(this.word);
            if (this.current === this.answered) {
                document.getElementById("input").value = "_".repeat(this.word.word.length);
                document.getElementById("input").setSelectionRange(0, 0);
            }
        },
        change() {
            let e = document.getElementById("input");
            let t = e.value, n = t.indexOf("_"), o;
            if (n == this.word.word.length) {
                if (t.substring(0, t.length - 1).toLowerCase() == this.word.word.toLowerCase()) {
                    ElMessage({
                        title: "Success",
                        message: "正确",
                        type: "success"
                    });
                    this.answered++;
                    this.next();
                    return;
                }
                ElMessage({
                    title: "Error",
                    message: "错误",
                    type: "error"
                }),
                    o = "_".repeat(this.word.word.length);
                n = 0;
            } else
                n == -1 ? o = "_".repeat(this.word.word.length) : (o = t.substring(0, n),
                    o += "_".repeat(this.word.word.length - n));
            e.value = o;
            e.setSelectionRange(n, n);
            e.focus();
        },
        audio_play() {
            var t = new Audio("https://dict.youdao.com/dictvoice?audio=" + this.word.word);
            t.play();
        },
        terminate() {
            ElMessageBox.confirm("确定要终止吗？")
                .then(() => {
                    this.mode = 0;
                    ElNotification({
                        message:"背诵已终止",
                        type:"info",
                        title:"Terminate"
                    });
                });
        }
    },
    created() {
        for (let i of window.wordsets) {
            this.wordsets.push(i.name);
        }
    },
}
</script>

<style scoped>
.title {
    font-size: 50px;
    font-weight: 800;
    color: #3c3c3c;
}

.checkbox {
    font-size: 30px;
}

#status {
    font-size: 20px;
}

.answer {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 70px;
    letter-spacing: 30px;
    border: none;
    outline-style: none;
    overflow: hidden;
    height: 100px;
    padding: 0;
    transition: .5s;
    resize: none;
    min-height: 100px;
}

#test {
    padding: 50px;
    animation: enter .5s ease-out;
}

#explain {
    font-size: 25px;
    margin-top: 20px;
}

#trans {
    font-size: 50px;
    transition: .5s;
}
</style>