<template>
    <el-container v-if="mode < 2" class="container">
        <el-header id="header" class="colbox">
            <div class="no">共有{{ wordsets.length }}个单词本</div>
            <div>
                <el-button @click="export_set" type="success"><box-icon color="white" name='export'
                        size="15px"></box-icon>导出</el-button>
                <el-button @click="import_set" type="warning"><box-icon color="white" name='import'
                        size="15px"></box-icon>导入</el-button>
                <el-button @click="mode = 1" type="primary"><box-icon color="white" name='plus'></box-icon>新建单词本</el-button>
            </div>
        </el-header>
        <el-main>
            <div v-if="mode === 0" class="colbox">
                <div v-for="(wordset, index) in wordsets" :key="index" class="wordset rowbox">
                    <div class="no">
                        {{ index + 1 }}
                    </div>
                    <div class="title">
                        {{ wordset.name }}
                    </div>
                    <div style="font-size: 17px;font-weight: 200;">
                        创建日期：{{ (new Date(wordset.created)).toLocaleString() }}
                    </div>
                    <div class="option">
                        <box-icon class="btn" name='edit' @click="edit(wordset.name, index)"></box-icon>
                        <box-icon class="btn" name='trash' @click="del(wordset.name, index)"></box-icon>
                    </div>
                </div>
            </div>
            <div v-if="mode === 1" id="new" class="rowbox">
                <el-page-header @back="mode = 0">
                    <template #content>
                        <span class="text-large font-600 mr-3"> 新建单词本 </span>
                    </template>
                </el-page-header>
                <div class="colbox">
                    <el-text class="mx-1" style="width: 50px;">名称:</el-text>
                    <el-input v-model="set_name"></el-input>
                </div>
                <el-button style="width: 50%;" type="primary" @click="new_wordset()">创建</el-button>
            </div>
        </el-main>
    </el-container>
    <div v-if="mode === 2" class="container" style="overflow: auto;">
        <el-page-header @back="mode = 0">
            <template #content>
                <span class="text-large font-600 mr-3"> {{ editing }} </span>
            </template>
        </el-page-header>
        <div class="colbox" style="margin-top: 20px;">
            <div class="rowbox">
                <div class="card" style="margin-bottom: 20px;">
                    <div><el-text class="mx-1" style="font-size: 35px;font-weight: 800;">添加单词</el-text></div>
                    <el-input v-model="new_word"></el-input>
                    <div class="colbox">
                        <el-text class="mx-1" style="width: 60px;">翻译:</el-text>
                        <el-input v-model="new_trans"></el-input>
                    </div>
                    <div class="colbox">
                        <el-text class="mx-1" style="width: 80px;">词性：</el-text>
                        <el-select v-model="word_type" class="m-2" placeholder="Select">
                            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value" />
                        </el-select>
                    </div>
                    <el-button @click="add_word()" type="primary">添加</el-button>
                </div>
                <div class="card">
                    <div><el-text class="mx-1" style="font-size: 35px;font-weight: 800;">更改名称</el-text></div>
                    <div class="colbox">
                        <el-text class="mx-1" style="width: 60px;">新名称:</el-text>
                        <el-input v-model="new_name"></el-input>
                    </div>
                    <el-button @click="change_name()" type="primary">更改名称</el-button>
                </div>
            </div>
            <div style="width: 100%;" class="card">
                <el-table :data="table" style="max-height: 550px;overflow: auto;border-radius: 10px;">
                    <el-table-column type="index" />
                    <el-table-column prop="word" label="单词" />
                    <el-table-column prop="type" label="词性" />
                    <el-table-column prop="trans" label="翻译" />
                    <el-table-column fixed="right" label="操作">
                        <template #default="scope">
                            <div style="display: flex;flex-direction: row;">
                                <box-icon size="xs" class="btn" name='trash' @click="del_word(scope.$index)"></box-icon>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
import { ElNotification, ElMessage, ElMessageBox } from 'element-plus';

export default {
    name: "NoteEditor",
    data() {
        return {
            mode: 0,
            wordsets: window.wordsets,
            set_name: "",
            editing: "",
            editingIndex: -1,
            new_word: "",
            new_name: "",
            word_type: "adj.",
            options: [{ label: "verb(v.)", value: "v." }, { label: "adverb(adv.)", value: "adv." }, { label: "noun(v.)", value: "n." }, { label: "adjective(adj.)", value: "adj." }, { label: "prepositions(prep.)", value: "prep." },],
            table: [],
            new_trans: "",
        }
    },
    methods: {
        new_wordset() {
            let res = localStorage.getItem(this.set_name);
            if (!res) {
                this.wordsets.push({
                    name: this.set_name,
                    created: (new Date()).getTime()
                });
                localStorage.setItem("wordsets", JSON.stringify(this.wordsets));
                localStorage.setItem(this.set_name, JSON.stringify([]));
                this.mode = 0;
            }
            else ElNotification("该名称已经被占用");
            return;
        },
        del(name, index) {
            ElMessageBox.confirm("确定要删除吗？")
                .then(() => {
                    localStorage.removeItem(name);
                    this.wordsets.splice(index, 1);
                    localStorage.setItem("wordsets", JSON.stringify(this.wordsets));
                    ElMessage(`已经删除 ${name} `);
                });
        },
        edit(name, index) {
            this.editing = name;
            this.table = JSON.parse(localStorage.getItem(name));
            this.mode = 2;
            this.editingIndex = index;
        },
        add_word() {
            this.table.push({
                word: this.new_word,
                type: this.word_type,
                trans: this.new_trans
            })
            localStorage.setItem(this.editing, JSON.stringify(this.table));
            ElMessage({
                message: `已添加 ${this.new_word} (${this.word_type})`,
                type: 'success',
            });
        },
        change_name() {
            let res = localStorage.getItem(this.new_name);
            if (!res) {
                localStorage.setItem(this.new_name, JSON.stringify(this.table));
                localStorage.removeItem(this.editing);
                this.wordsets[this.editingIndex].name = this.new_name;
                localStorage.setItem("wordsets", JSON.stringify(this.wordsets));
                ElMessage({
                    message: `已更改 ${this.editing} 为 ${this.new_name}`,
                    type: 'success',
                });
                this.editing = this.new_name;
            }
            else ElNotification("该名称已经被占用");
            return;
        },
        del_word(index) {
            this.table.splice(index, 1);
            localStorage.setItem(this.editing, JSON.stringify(this.table));
        },
        async export_set() {
            let fileHandle = await window.showSaveFilePicker({
                types: [
                    {
                        description:"JSON file",
                        accept: {
                            'text/json': ['.json'],
                        },
                    },
                ],
            });
            let writestream = await fileHandle.createWritable();
            let data = {
                wordsets:this.wordsets,
                words:{}
            };
            for(let i of this.wordsets){
                let temp = JSON.parse(localStorage.getItem(i.name));
                data.words[i.name] = temp;
            }
            writestream.write(JSON.stringify(data));
            writestream.close();
        },
        async import_set(){
            let [fileHandle] = await window.showOpenFilePicker({
                types: [
                    {
                        description:"JSON file",
                        accept: {
                            'text/json': ['.json'],
                        },
                    },
                ],
            });
            let file = await fileHandle.getFile();
            let data = JSON.parse(await file.text());
            let cnt = 0;
            for(let i of data.wordsets){
                if(!localStorage.getItem(i.name)){
                    this.wordsets.push(i);
                    localStorage.setItem(i.name,JSON.stringify(data.words[i.name]));
                    cnt++;
                }
            }
            localStorage.setItem("wordsets",JSON.stringify(this.wordsets));
            ElNotification({
                type:"success",
                title:"添加成功",
                message:`已添加 ${cnt} 本单词本`
            });
        }
    }
}
</script>

<style scoped>
#header {
    border-bottom: solid 1px #bcbcbc;
    justify-content: space-between;
    height: 40px;
}

.no {
    font-size: 20px;
    font-weight: 700;
}

.title {
    margin-top: 20px;
    font-size: 35px;
    flex-grow: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.option {
    display: flex;
    justify-content: space-around;
    opacity: 0;
    transition: .5s;
}

.wordset:hover .option {
    opacity: 1;
}

.card div {
    margin-bottom: 10px;
}

#new div {
    margin-bottom: 20px;
}
</style>
